* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.raindrop {
  position: absolute;
  background-color: rgba(137, 187, 204, 0.7);
  border-radius: 50%;
  animation: fall linear infinite;
  width: 2vmin;
  height: 2vmin;
}

@keyframes fall {
  0% {
    transform: translateY(-100vh);
  }
  100% {
    transform: translateY(100vh);
  }
}

.cloud {
  position: absolute;
  bottom: 90%;
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0.8;
  animation: moveClouds linear infinite;
  box-shadow: 0 0.4vmin 1vmin rgba(0, 0, 0, 0.3);
  width: 20vmin;
  height: 10vmin;
}

@keyframes moveClouds {
  0% {
    transform: translateX(-100vw);
  }
  100% {
    transform: translateX(100vw);
  }
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ee98b7;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.Box {
  width: 75vmin;
  height: 90vmin;
  border-radius: 2%;
  background-color: #413d3f;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  flex-direction: column;
  padding: 2vmin;
  max-width: 100%;
  max-height: 100%;
}

.Box img {
  height: 75vmin;
  width: 75vmin;
  margin-bottom: 50vmin;
}

.Box h1 {
  font-size: 4vmin;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
  color: #fff;
  text-shadow: 0.2vmin 0.2vmin 0.5vmin rgba(0, 0, 0, 0.3);
  padding: 2vmin;
  text-align: center;
  max-width: 80%;
  margin-top: 35vmin;
}

.Box button {
  padding: 2vmin 4vmin;
  font-size: 4vmin;
  border-radius: 5vmin;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 0.4vmin 0.6vmin rgba(0, 0, 0, 0.2);
}

.ButtonContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75vmin;
  position: absolute;
  bottom: 2.5vmin;
  gap: 10vmin;
}


.Yes {
  background: linear-gradient(145deg, #30e080, #2dbf6b);
  color: white;
  text-shadow: 0.1vmin 0.1vmin 0.3vmin rgba(0, 0, 0, 0.1);
}

.No {
  background: linear-gradient(145deg, #ff4d4d, #e62424);
  color: white;
  text-shadow: 0.1vmin 0.1vmin 0.3vmin rgba(0, 0, 0, 0.1);
}

.Yes:hover {
  transform: translateY(-1vmin);
  box-shadow: 0 1vmin 2vmin rgba(0, 0, 0, 0.6);
}

.No:hover {
  transform: translateY(-1vmin);
  box-shadow: 0 1vmin 2vmin rgba(0, 0, 0, 0.6);
}

.Yes:active,
.No:active {
  transform: translateY(0.1vmin);
  box-shadow: 0 0.4vmin 0.6vmin rgba(0, 0, 0, 0.2);
}
